#fileInput {
    display: none;
}

.write {
    padding-top: 50px;
}

.writeForm {
    position: relative;
}

.writeFormGroup {
    margin-left: 150px;
    display: flex;
    align-items: center;
}

.writeIcon {
    width: 25px;
    height: 25px;
    border: 1px solid;
    border-radius: 50%; 
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: #666;
    cursor: pointer;
}

.writeInput {
    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;
}

.writeInput:focus {
    outline: none;
}

.writeText {
    font-size: 20px;
    height: 100vh;
}

.writeButton {
    position: absolute;
    top: 20px;
    right: 50px;
    color: white;
    background-color: rgb(19, 106, 19);
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
}

.writeImg {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 70vw;
    height: 250px;
    border-radius: 10px;
    object-fit: cover;
}

